export const compareWithToday = (tooday: moment.Moment, date: string) => {
  const myDate = DATE(date, "MM/DD/YYYY");
  let todayConverted =
    (tooday.get("month") < 9 ? "0" : "") +
    (tooday.get("month") + 1).toString() +
    "/" +
    tooday.get("date") +
    "/" +
    tooday.get("year");
  if (new Date(myDate).getTime() < new Date(todayConverted).getTime()) {
    return true;
  }
  return false;
};

export interface ExamDoneDetailsAnswers {
  course: string;
  course_name: string;
  discription: string;
  termes: string;
  rules: {
    right_click_enabled: boolean;
    copy_enabled: boolean;
    paste_enabled: boolean;
    stop_candidate_when_comeback: boolean;
    allow_to_leave_browser: boolean;
  };
  period: string;
  exam_date: string;
}

interface NotPreparedExam {
  course: string;
  course_name: string;
  course_code: string;
  position: string;
}
interface changeLocProps {
  changeLocation: () => void;
}
interface CandidateExamsProps {
  FC_GetProfileDetails: Function;
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  setSelectedCandidateExam: (exam_id: string) => void;
  setSelectedCourse: (course: course | null) => void;
  setSelectedPosition: (position: PositionAPI) => void;
  InitializeExamDetails: (
    periodId: string,
    courseId: string,
    district_id: string,
    center_id: string,
    room_id: string,
    position_id: string,
    callback: (loading: boolean) => void
  ) => void;
  loadExamsDone: (
    periodId: string,
    userId: string,
    callback: (loading: boolean) => void
  ) => void;
  AddUserApplication: (application: GetApplicationsAPI) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
}

const ChangeLocationComponent = (props: changeLocProps): JSX.Element => {
  const [display, setDisplay] = useState<boolean>(true);
  if (display === false) {
    return <div></div>;
  }
  return (
    <div className="absolute right-2 left-2 md:right-1/4 md:left-1/4 lg:left-1/2 lg:right-auto bg-yellow-50 shadow-xl rounded py-1 px-1 flex flex-row justify-between items-center gap-2 text-yellow-800 -mt-5 lg:-mt-8">
      <div className="flex flex-row gap-2 items-center justify-between w-full pl-1">
        <span className="font-bold">You are advised to change location</span>
        <div
          onClick={props.changeLocation}
          className="px-2 py-1 rounded bg-blue-100 text-blue-600 w-max cursor-pointer"
        >
          Change
        </div>
      </div>
      <div
        onClick={() => setDisplay(false)}
        className="bg-yellow-100 text-yellow-600 rounded-full flex items-center justify-center p-1 h-8 w-8 cursor-pointer"
      >
        <MdClose className="text-4xl" />
      </div>
    </div>
  );
};

interface candidatePendingExamsList {
  preparedExam: UserExamsListInterface[];
  notPreparedExam: NotPreparedExam[];
}
interface CandidateExamsState {
  loading: boolean;
  loadingExamsDone: boolean;
  candidateExams: candidatePendingExamsList | null;
  activeExams: string[];
  candidateApplications: GetApplicationsAPI[] | null;
  selectedCourse: course | null;
  centersModal: boolean;
  maxExamModal: boolean;
  selectedPosition: string;
  successMessage: string;
  selectedExam: ExamDoneDetailsAnswers | null;
}

export class _CandidateExams extends Component<
  CandidateExamsProps,
  CandidateExamsState
> {
  constructor(props: CandidateExamsProps) {
    super(props);

    this.state = {
      loading: false,
      loadingExamsDone: false,
      candidateExams: null,
      activeExams: [],
      candidateApplications: null,
      selectedCourse: null,
      centersModal: false,
      maxExamModal: false,
      selectedPosition: "",
      successMessage: "",
      selectedExam: null,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  setloadingExamsDone = (status: boolean) => {
    this.setState({ loadingExamsDone: status });
  };
  loadCandidateExams = async (user: string, period: string) => {
    this.setLoading(true);
    setAxiosToken();
    try {
      const res = await axios.get<candidatePendingExamsList>(
        `${API.QBank}/exam/user/${user}/${period}`
      );
      this.setState({ candidateExams: res.data });
      // console.log("My exams list: ", res.data);
    } catch (error: any) {
      console.log("My exams error: ", { ...error });
      alert(errorToText(error));
    }
    this.setLoading(false);
  };
  loadUserProfileDetails = () => {
    this.setState({
      loading: true,
    });
    this.props.FC_GetProfileDetails(
      this.props.auth.user.user_id,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
          });
        } else {
          this.setState({
            loading: false,
          });
        }
      }
    );
    this.setState({
      loading: false,
    });
  };

  InitializationFn = (application: GetApplicationsAPI) => {
    if (this.props.candidateExam.user_exams_done.length === 0) {
      this.props.loadExamsDone(
        application.application_session.session_plan_id,
        this.props.auth.user.user_id,
        this.setloadingExamsDone
      );
    }
    for (let course of application.position.courses) {
      let testDone = this.props.candidateExam.user_exams_temp.find(
        (itm) => itm.course === course.course_id
      );
      if (testDone === undefined) {
        let center_id = application.exams.find(
          (itm) => itm.course_id === course.course_id
        );
        if (center_id !== undefined) {
          this.props.InitializeExamDetails(
            application.application_session.session_plan_id,
            course.course_id,
            center_id.district_id,
            center_id.center_id,
            center_id.room_id,
            application.position.position_id,
            this.setLoading
          );
        }
      }
    }
  };
  loadUserApplications = async () => {
    this.setLoading(true);
    try {
      const res = await axios.get<GetApplicationsAPI[]>(
        `${API.APPLICATION}/application/${this.props.auth.user.user_id}`
      );
      if (res.status === 200) {
        this.setState({ candidateApplications: res.data });
        for (let application of res.data) {
          this.InitializationFn(application);
        }
        if (
          this.props.candidateApplications.candidate_applications === null ||
          this.props.candidateApplications.candidate_applications.length === 0
        ) {
          for (let application of res.data) {
            this.props.AddUserApplication(application);
          }
        }
      }
    } catch (error: any) {
      console.log("Appl ex Err: ", { ...error });
    }
    this.setLoading(false);
  };
  findLocation = (position_id: string, course_id: string) => {
    if (this.state.candidateApplications !== null) {
      const test = this.state.candidateApplications.find(
        (item) =>
          item.position.position_id === position_id &&
          item.exams.filter((itm) => itm.course_id === course_id)
      );
      if (test !== undefined) {
        let course = test.exams.find((itm) => itm.course_id === course_id);
        if (course !== undefined) {
          return course;
        }
      }
    }
    return null;
  };
  updateLocation = async (
    application_id: string,
    exams: sentExamInterface[]
  ) => {
    this.setState({ loading: true });
    for (let i in exams) {
      try {
        const res = await axios.post(
          `${API.APPLICATION}/application/updatemissed`,
          {
            application_id: application_id,
            exams: exams[i],
          }
        );
        if (res.status === 200) {
          this.setState({
            selectedCourse: null,
            centersModal: false,
            maxExamModal: false,
            selectedPosition: "",
            successMessage: "Location updated successfully!",
          });
          this.loadUserApplications();
        }
      } catch (error: any) {
        console.log("Update loc err: ", { ...error });
      }
    }
  };
  updateLocationFunction = (exams: sentExamInterface[]) => {
    if (this.state.candidateApplications !== null) {
      let application = this.state.candidateApplications.find(
        (item) => item.position.position_id === this.state.selectedPosition
      );
      if (application !== undefined) {
        this.updateLocation(application.application_id, exams);
      } else {
        alert("Invalid application");
      }
    } else {
      alert("Missing application");
    }
  };
  addLocation = (
    course_id: string,
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string
  ) => {
    return this.updateLocationFunction([
      {
        course_id: course_id,
        proctoring_type: "Center",
        center_id: location.center_id,
        district_id: location.district_id,
      },
    ]);
  };
  componentDidMount = () => {
    this.props.loadApplicationPeriods(this.setLoading);

    if (
      this.props.auth.user.sex === "" ||
      this.props.auth.user.sex.length === 0
    ) {
      this.loadUserProfileDetails();
    }
    this.loadUserApplications();
    if (this.props.auth.application_session !== null) {
      this.loadCandidateExams(
        this.props.auth.user.user_id,
        this.props.auth.application_session.session_plan_id
      );
    }
    if (this.props.auth.application_session !== null) {
      this.props.loadExamsDone(
        this.props.auth.application_session.session_plan_id,
        this.props.auth.user.user_id,
        this.setloadingExamsDone
      );
    }
  };

  removeDuplicatesExams = (
    exams: UserExamsListInterface[]
  ): UserExamsListInterface[] => {
    let temp: UserExamsListInterface[] = [];
    if (this.state.candidateApplications !== null) {
      for (let item of exams) {
        if (
          this.state.candidateApplications.find(
            (test) => test.position.position_id === item.position
          ) !== undefined
        ) {
          if (temp.find((itm) => itm._id === item._id) === undefined) {
            temp = [...temp, item];
          }
        }
      }
    }
    return temp;
  };

  removeDuplicatesNotPreparedExams = (
    exams: NotPreparedExam[]
  ): NotPreparedExam[] => {
    let temp: NotPreparedExam[] = [];
    if (this.state.candidateApplications !== null) {
      for (let item of exams) {
        if (
          this.state.candidateApplications.find(
            (test) => test.position.position_id === item.position
          ) !== undefined
        ) {
          if (temp.find((itm) => itm.course === item.course) === undefined) {
            temp = [...temp, item];
          }
        }
      }
    }
    return temp;
  };
  setTimeOut = (exam_id: string) => {
    let temps: string[] = this.state.activeExams;
    this.setState({
      activeExams: [...temps, exam_id],
    });
  };
  selectApplication = (
    applications: GetApplicationsAPI[] | null,
    position_id: string,
    course_id: string
  ): GetApplicationsAPI | null => {
    if (applications !== null) {
      let temp = applications.find(
        (item) => item.position.position_id === position_id
      );
      if (temp !== undefined) {
        if (
          temp.position.courses.find((itm) => itm.course_id === course_id) !==
          undefined
        ) {
          return temp;
        }
      }
    }
    return null;
  };
  setMaxExamModal = (status: boolean) => {
    this.setState({ maxExamModal: status });
  };

  changeLocation = (item: PositionAPICourse) => {
    this.setState({ selectedCourse: item, centersModal: true });
    this.setMaxExamModal(true);
  };

  closeModal = () => {
    this.setState({ centersModal: false });
    this.setMaxExamModal(false);
  };

  render() {
    const today = moment().tz(TimeZone);
    // console.log("TODAY: ", today.get("days"));
    if (!FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).view) {
      return <Redirect to="/" />;
    }
    if (this.props.candidateExam.selected_exam_id !== null) {
      return <Redirect to="/do-exam" />;
    }
    return (
      <Fragment>
        {this.state.selectedCourse === null ? (
          <div className="mt-2 -ml-2 mr-3 pb-5">
            <div className="rounded mt-2">
              <div className="flex flex-row items-center gap-3">
                <Link
                  to="/apps-page"
                  title="Back to menus"
                  className="bg-white group hover:bg-blue-50 h-10 w-10 flex items-center justify-center rounded-full cursor-pointer"
                >
                  <BiArrowBack className="text-3xl text-blue-600 group-hover:text-blue-800" />
                </Link>
                <div className="flex flex-col">
                  <span className="font-extrabold text-3xl">My exams</span>
                  <span className="text-gray-600 text-sm">
                    List of exams that are assigned to me
                  </span>
                </div>
              </div>
            </div>
            {this.state.successMessage !== "" && (
              <div
                title="Click to close the message"
                onClick={() => this.setState({ successMessage: "" })}
                className="animate__animated animate__zoomIn px-3 py-2 bg-green-500 text-white my-2 rounded cursor-pointer"
              >
                {this.state.successMessage}
              </div>
            )}
            {this.state.loading === true ||
            this.state.loadingExamsDone === true ||
            this.state.candidateExams === null ||
            this.state.candidateApplications == null ? (
              <div className="px-4 pt-5 pb-1 bg-white mt-2 rounded">
                <Loading />
              </div>
            ) : this.removeDuplicatesExams(
                this.state.candidateExams.preparedExam
              ).length === 0 ? (
              <div className="bg-gray-50 rounded px-4 py-5 text-center font-bold mt-2 text-2xl text-gray-400">
                No exams found
              </div>
            ) : (
              this.removeDuplicatesExams(
                this.state.candidateExams.preparedExam
              ).map((item, i) => (
                <div key={i + 1} className="my-2 rounded bg-white p-2 group">
                  <div className="flex flex-col md:flex-row items-center justify-between">
                    <div className="flex flex-col md:flex-row items-center gap-2 w-full">
                      <div>
                        <BsFillFileEarmarkTextFill className="hidden md:block text-4xl text-gray-600 group-hover:text-blue-600" />
                      </div>
                      <div className="flex flex-col w-full">
                        <span className="text-base text-gray-800 font-extrabold group-hover:text-blue-600">
                          {item.course_name}
                        </span>
                        <div className="flex flex-row items-center justify-start w-full gap-3 -mt-1">
                          <div className="text-sm text-gray-500 group-hover:text-blue-600">
                            Date: {DATE(item.exam_date, "DD/MM/YYYY")}
                          </div>
                          <div className="hidden md:block text-sm bg-gray-100 underline text-gray-500  font-bold rounded-full px-2">
                            Start time: {item.start_time}
                          </div>
                          <div className="hidden md:block text-sm bg-blue-100 text-blue-900  font-bold rounded-full px-2">
                            Status: {item.user_status}
                          </div>
                        </div>
                        {this.findLocation(item.position, item.course) !==
                          null && (
                          <div className="flex flex-row items-center gap-2 mt-2">
                            <div>
                              Exam center:{" "}
                              <span className="text-sm bg-gray-100 text-blue-900 group-hover:text-blue-600 font-bold rounded-full px-2 w-max">
                                {
                                  this.findLocation(item.position, item.course)
                                    ?.center_name
                                }
                              </span>
                            </div>
                            <div className="w-max flex flex-col md:flex-row">
                              Room number:{" "}
                              {this.findLocation(item.position, item.course)
                                ?.room_number === 0 ? (
                                <span className="text-sm bg-yellow-100 text-yellow-900 group-hover:text-yellow-800 font-bold rounded-full px-2 w-max flex items-center justify-center">
                                  Waiting room
                                </span>
                              ) : (
                                <span className="text-sm bg-gray-100 text-blue-900 group-hover:text-blue-600 font-bold rounded-full px-2 w-max">
                                  {
                                    this.findLocation(
                                      item.position,
                                      item.course
                                    )?.room_number
                                  }
                                </span>
                              )}
                              {/* Change loc */}
                              {this.findLocation(item.position, item.course)
                                ?.room_number === 0 &&
                                validateExamDate(
                                  item.exam_date,
                                  item.start_time,
                                  item.end_time
                                ) !== ExamTimeStatus.Expired && (
                                  <ChangeLocationComponent
                                    changeLocation={() => {
                                      this.setState({
                                        selectedCourse: {
                                          course_name: item.course_name,
                                          course_code: item.course_code,
                                          description: item.course_name,
                                          school_levels: [],
                                          course_id: item.course,
                                        },
                                        selectedPosition: item.position,
                                        centersModal: true,
                                      });
                                      this.setMaxExamModal(true);
                                    }}
                                  />
                                )}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="lg:w-full flex flex-row items-center justify-end">
                      {this.state.activeExams.find(
                        (itm) => itm === item._id
                      ) !== undefined ||
                      item.user_status === UserExamStatus.Editing ||
                      item.user_status === UserExamStatus.Paused ? (
                        validateExamDate(
                          item.exam_date,
                          item.start_time,
                          item.end_time
                        ) !== ExamTimeStatus.Expired ? (
                          this.selectApplication(
                            this.state.candidateApplications,
                            item.position,
                            item.course
                          )?.application_status !==
                          ApplicationStates.Shortlisted ? (
                            <div className="text-yellow-800 font-extrabold text-base bg-yellow-100 rounded px-3 py-1 cursor-not-allowed">
                              Not shortlisted
                            </div>
                          ) : (
                            <div
                              onClick={() => {
                                let application = this.selectApplication(
                                  this.state.candidateApplications,
                                  item.position,
                                  item.course
                                );
                                if (application !== null) {
                                  if (
                                    application.application_status ===
                                    ApplicationStates.Shortlisted
                                  ) {
                                    // Select exam
                                    this.props.setSelectedPosition(
                                      application.position
                                    );
                                    this.props.setSelectedCourse(
                                      application.position.courses.find(
                                        (itm) => itm.course_id === item.course
                                      )!
                                    );
                                    this.props.setSelectedCandidateExam(
                                      item._id
                                    );
                                  } else {
                                    alert(
                                      "Corresponding application is not shortlisted!"
                                    );
                                  }
                                } else {
                                  alert("Invalid exam!");
                                }
                              }}
                              className={`${
                                item.user_status === UserExamStatus.Editing ||
                                item.user_status === UserExamStatus.Paused
                                  ? "bg-green-600 group-hover:bg-green-800"
                                  : "bg-blue-600 group-hover:bg-blue-800"
                              } btn  text-white cursor-pointer w-max`}
                            >
                              {item.user_status === UserExamStatus.Editing
                                ? "Back to Exam"
                                : item.user_status === UserExamStatus.Paused
                                ? "Paused, back to exam"
                                : "Start Exam"}
                            </div>
                          )
                        ) : item.user_status === UserExamStatus.Editing ||
                          item.user_status === UserExamStatus.Done ||
                          item.user_status === UserExamStatus.Paused ||
                          item.user_status === UserExamStatus.Removed ? (
                          <div className="text-blue-700 flex flex-col items-end justify-center w-max">
                            <span>Marks</span>
                            <span className="text-2xl font-extrabold -mt-2">
                              {item.user_marks.toFixed(2)}%
                            </span>
                            <div
                              onClick={() =>
                                this.setState({
                                  selectedExam: {
                                    course: item.course,
                                    course_name: item.course_name,
                                    discription: item.discription,
                                    termes: item.termes,
                                    rules: item.rules,
                                    period: item.period,
                                    exam_date: item.exam_date,
                                  },
                                })
                              }
                              className="btn bg-blue-100 text-blue-700 cursor-pointer"
                            >
                              Check details
                            </div>
                          </div>
                        ) : (
                          <div className="bg-yellow-100 text-yellow-700 font-extrabold text-base px-3 py-1 rounded-md w-max">
                            Expired
                          </div>
                        )
                      ) : item.found === false ? (
                        <div
                          title="Please wait for the exam time"
                          className="text-lg lg:text-xl font-extrabold bg-green-100 text-green-700 px-3 py-1 rounded cursor-not-allowed w-max"
                        >
                          <CountDown
                            start_time={new Date(today.toString()).toString()}
                            duration_min={getMinutesFromTwoDates(
                              new Date(today.toString()),
                              new Date(
                                DATE(item.exam_date, "MM/DD/YYYY") +
                                  " " +
                                  item.start_time
                              )
                            )}
                            timeout={() => this.setTimeOut(item._id)}
                            returnString={true}
                            labels={true}
                            days
                          />
                        </div>
                      ) : (
                        <div className="text-blue-700 flex flex-col items-end justify-center w-max">
                          <span>Marks</span>
                          <span className="text-2xl font-extrabold -mt-2">
                            {item.user_marks.toFixed(2)}%
                          </span>
                          <div
                            onClick={() =>
                              this.setState({
                                selectedExam: {
                                  course: item.course,
                                  course_name: item.course_name,
                                  discription: item.discription,
                                  termes: item.termes,
                                  rules: item.rules,
                                  period: item.period,
                                  exam_date: item.exam_date,
                                },
                              })
                            }
                            className="btn bg-blue-100 text-blue-700 cursor-pointer"
                          >
                            Check details
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))
            )}
            {/* Not prepared exams */}
            {this.state.candidateExams !== null &&
              this.state.loading !== true &&
              this.removeDuplicatesNotPreparedExams(
                this.state.candidateExams.notPreparedExam
              ).length > 0 &&
              this.removeDuplicatesNotPreparedExams(
                this.state.candidateExams.notPreparedExam
              ).map((item, i) => (
                <div
                  key={i + 1}
                  className="my-2 rounded bg-white p-2 py-3 group"
                >
                  <div className="flex flex-col md:flex-row items-center justify-between">
                    <div className="flex flex-col md:flex-row items-center gap-2 w-full">
                      <div className="">
                        <BsFillFileEarmarkTextFill className="hidden md:block text-4xl text-gray-300 group-hover:text-blue-600" />
                      </div>
                      <div className="flex flex-col">
                        <span className="text-base text-gray-600 font-bold group-hover:text-blue-600">
                          {item.course_name}
                        </span>
                        <div className="flex flex-row items-center gap-3 -mt-1">
                          {this.findLocation(item.position, item.course) !==
                            null && (
                            <div className="flex flex-row items-center gap-2 mt-2">
                              <div className="flex flex-col md:flex-row md:items-center">
                                Exam center:{" "}
                                <span className="text-sm bg-gray-100 text-blue-900 group-hover:text-blue-600 font-bold rounded-full px-2 w-max">
                                  {
                                    this.findLocation(
                                      item.position,
                                      item.course
                                    )?.center_name
                                  }
                                </span>
                              </div>
                              <div className="w-max flex flex-col md:flex-row md:items-center">
                                Room number:{" "}
                                {this.findLocation(item.position, item.course)
                                  ?.room_number === 0 ? (
                                  <span className="text-sm bg-yellow-100 text-yellow-900 group-hover:text-yellow-800 font-bold rounded-full px-2 w-max flex items-center justify-center">
                                    Waiting room
                                  </span>
                                ) : (
                                  <span className="text-sm bg-gray-100 text-blue-900 group-hover:text-blue-600 font-bold rounded-full px-2 w-max">
                                    {
                                      this.findLocation(
                                        item.position,
                                        item.course
                                      )?.room_number
                                    }
                                  </span>
                                )}
                                {/* Change loc */}
                                {this.findLocation(item.position, item.course)
                                  ?.room_number === 0 && (
                                  <ChangeLocationComponent
                                    changeLocation={() => {
                                      this.setState({
                                        selectedCourse: {
                                          course_name: item.course_name,
                                          course_code: item.course_code,
                                          description: item.course_name,
                                          school_levels: [],
                                          course_id: item.course,
                                        },
                                        selectedPosition: item.position,
                                        centersModal: true,
                                      });
                                      this.setMaxExamModal(true);
                                    }}
                                  />
                                )}
                              </div>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                    <div className="bg-gray-100 text-gray-600 font-extrabold text-base px-3 py-1 rounded-md">
                      Not prepared
                    </div>
                  </div>
                </div>
              ))}
          </div>
        ) : this.state.loading === true ? (
          <div className="px-4 pt-5 pb-1 bg-white mt-2 rounded">
            <Loading />
          </div>
        ) : (
          <div className="animate__animated animate__fadeInUp animate__faster mt-2 mr-1 -ml-3 rounded-t-md bg-white shadow-md pb-5">
            <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2 mb-2">
              <div className="flex flex-col w-full">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Change exam location
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() => {
                        this.setState({
                          centersModal: false,
                          selectedCourse: null,
                        });
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <ChooseExamCenter
              positionCourses={this.state.selectedCourse}
              addLocation={this.addLocation}
              closeModal={this.closeModal}
            />
          </div>
        )}
        {this.state.selectedExam !== null && (
          <Modal
            title={
              <div className="text-2xl font-extrabold text-blue-600 px-2">
                Exam result details
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => this.setState({ selectedExam: null })}
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{ title: true }}
          >
            <CandidateAnswersDetails
              exam_id={this.state.selectedExam}
              showAnswers={false}
            />
          </Modal>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  candidateExam,
  candidateApplications,
  periods,
}: StoreState): {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
  periods: applicationPeriodsStoreInterface;
} => {
  return {
    auth: auth,
    candidateExam: candidateExam,
    candidateApplications: candidateApplications,
    periods: periods,
  };
};

export const CandidateExams = connect(mapStateToProps, {
  FC_GetProfileDetails,
  LoadCandidateApplications,
  setSelectedCandidateExam,
  setSelectedCourse,
  setSelectedPosition,
  InitializeExamDetails,
  loadExamsDone,
  AddUserApplication,
  loadApplicationPeriods,
})(_CandidateExams);
